body {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color:#01204E;
}

#noButton {
  position: absolute;
  margin-left: 150px;
  transition: 0.5s;
}

#yesButton {
  position: absolute;
  margin-right: 150px;
}

.container {
  text-align: center;
}

.header_text {
  font-family: "Nunito", sans-serif;
  font-size: 40px;
  font-weight: bold;
  color: rgb(255, 255, 255);
  margin-top: 20px;
  margin-bottom: 0;
}

.pictures-container {
  display: flex;
  justify-content: space-around; /* To evenly space the images */
  margin-top: 20px;
}


img{
  border-radius: 5px;
}

.pictures-container img{
  width: 300px;
  height: 300px;
}

.mike,
.rachel {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.me,
.you {
  font-family: "Nunito", sans-serif;
  font-size: 18px;
  color: rgb(255, 255, 255);
  margin-top: 8px;
}

.final_container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.us {
  font-family: "Nunito", sans-serif;
  font-size: 18px;
  color: rgb(255, 255, 255);
  margin-top: 8px;
}

.main-gif {
  width: 100%; /* Make sure the image stretches to fit its container */
}

#yesButton{
  right: 760px;
}


.btn {
  background-color:#fff;
  color:hsla(40, 12%, 5%, 1) ;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border: none;
  border-radius: 12px;
  transition: background-color 0.3s ease;
}

@media only screen and (max-width: 320px) and (max-height: 568px) {
  body {
    height: 100vh;
  }

  .header_text {
    font-size: 20px;
  }

  .btn {
    padding: 10px 18px;
    font-size: 12px;
  }
}

@media only screen and (max-width: 414px) and (max-height: 736px) {
  body {
    height: 90vh;
  }

  .header_text {
    font-size: 28px;
  }

  .btn {
    padding: 15px 25px;
    font-size: 14px;
  }
}